'use client'
import { useState } from "react"
import { IUser } from "./userTypes"
import createUser from "./createUser"
import { useRouter } from "next/router"





export default function FormReg(){
    const [value,setValue]=useState<IUser>({name:'',password:''})
    const [err,setErr]=useState(false)
    const router=useRouter()
    const handleRedirect=()=>{
      
    }
    const validator=async (data:IUser)=>{
        const err=await createUser(data)
        if(err){
            setErr(true)
        }else{
              router.push('/')
            setErr(false)
        }
        
    }
    
    return <form className="flex" >
        <p>Name</p>
        <input className="bg-cyan-300" value={value.name} onChange={(e)=>setValue({...value,name:e.target.value})} type="text" />
         <p>Password</p>
        <input className="bg-cyan-300" value={value.password} onChange={(e)=>setValue({...value,password:e.target.value})} type="password" />
        <button onClick={(e)=>{e.preventDefault();validator(value)}} >Log</button>
    </form>
}